****
***/
div#torrent_inspector { overflow: auto; -webkit-overflow-scrolling: touch; text-align: left; padding: 15px; top: 68px; position: fixed; width: 570px; z-index: 5; border-left: 1px solid #888; bottom: 22px; right: 0px; /* Files Inspector Tab */ }

div#torrent_inspector #inspector-close { display: none; }

div#torrent_inspector #inspector-tabs-wrapper { width: 100%; overflow: hidden; text-align: center; }

